<template>
<multipane class="vertical-panes" layout="vertical" id="root">
  <div class="pane" :style="{ minWidth: '100px', width: '25%', maxWidth: '200px' }">
    <div>
      <img id="profile" src='../assets/me.jpg'>
      <b-nav-item to='/'>About Me</b-nav-item>
      <b-nav-item to='/blog'>Blogs</b-nav-item>
    </div>
  </div>
  <multipane-resizer></multipane-resizer>
  <div class="pane" id="content-pane" :style="{ flexGrow: 1 }">
    <div>
      <router-view></router-view>
    </div>
  </div>
</multipane>
</template>

<script>
import { Multipane, MultipaneResizer } from 'vue-multipane'

export default {
  name: 'HelloWorld',
  props: {
    msg: String
  },
  components: {
    Multipane,
    MultipaneResizer
  }
}
</script>

<style>
.vertical-panes {
  width: 100%;
  height: 600px;
  border: 1px solid #ccc;
}
.vertical-panes > .pane {
  text-align: left;
  padding: 15px;
  overflow: hidden;
  background: #eee;
}
.vertical-panes > .pane ~ .pane {
  border-left: 1px solid #ccc;
}

#profile {
  width: 70%;
  border-radius: 70%;
}

#content-pane {
  overflow-y: scroll;
  overflow-x: auto;
}
</style>

<style scoped>
#root {
  flex-grow: 1;
}
</style>



