import Vue from 'vue'
import Router from 'vue-router'
import about from '@/components/views/About'
import blog from '@/components/views/Blog'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      component: about
    },
    {
      path: '/blog',
      component: blog
    }
  ]
})



